// Pokemon Battle System - Worker with Auth, Database, and Real-time PvP

// Showdown完全データをインポート（データのみ）
import {
  EXTRACTED_TYPE_CHART as SHOWDOWN_TYPE_CHART,
  EXTRACTED_POKEDEX as SHOWDOWN_POKEDEX,
  EXTRACTED_MOVES as SHOWDOWN_MOVES,
} from './showdown-extracted-data';


// Battle Data
type MonsterType = 'normal' | 'fire' | 'water' | 'grass' | 'electric' | 'ice' |
  'fighting' | 'poison' | 'ground' | 'flying' | 'psychic' | 'bug' |
  'rock' | 'ghost' | 'dragon' | 'dark' | 'steel' | 'fairy' | string;

interface MoveTemplate {
  id: string;
  name: string;
  type: MonsterType;
  power: number;
  accuracy: number;
  pp: number;
  priority: number;
  category?: 'physical' | 'special' | 'status';
  drain?: number;   // HP吸収率 (0-100%)
  recoil?: number;  // 反動ダメージ率 (0-100%)
  secondary?: {
    chance: number;
    status?: string;
    boosts?: Record<string, number>;
  };
  effect?: {
    type: 'buff' | 'debuff';
    stat: 'atk' | 'def' | 'spa' | 'spd' | 'spe' | 'crit' | 'evade';
    stages: number;
    target: 'self' | 'opponent';
  };
}

interface MonsterTemplate {
  id: string;
  name: string;
  type: MonsterType;
  type2?: MonsterType;
  baseStats: { hp: number; atk: number; def: number; spa: number; spd: number; spe: number; };
  movePool?: string[];
  spriteUrl?: string;
  abilities: string[];
}

