// 便利関数
export function getPokemonById(id: string): ExtractedPokemon | undefined {
	return EXTRACTED_POKEDEX.find(p => p.id === id.toLowerCase());
}

export function getMoveById(id: string): ExtractedMove | undefined {
	return EXTRACTED_MOVES.find(m => m.id === id.toLowerCase().replace(/[^a-z0-9]/g, ''));
}
